@font-face {
  font-family: Avenir;
  font-weight: 300;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir;
  font-weight: 600;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}

@font-face {
  font-family: Avenir;
  font-weight: 900;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}

.page-content {
  margin: 0;
  padding: 0 8px;
}

.form-container {
  margin: 20px 0;
}

.input {
  box-sizing: border-box;
  color: #3d4e61;
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

.form--large .input {
  box-sizing: border-box;
  background-position: 26px;
  background-size: 19px 19px;
  height: 70px;
  padding: 0 0 0 62px;
  font-size: 16px;
}

.form--small .input {
  box-sizing: border-box;
  background-position: 12px 49%;
  background-size: 11px 11px;
  height: 42px;
  padding: 0 0 0 33px;
  font-size: 14px;
  font-weight: 400;
}

.form--large .input:hover, .form--small .input:hover {
  box-shadow: 0 4px 4px #00000040;
}

.form .input:focus {
  background-color: #f6f6f7;
  outline: none;
  font-weight: 900;
}

.form--large .input:focus, .form--small .input:focus {
  text-shadow: 1px 4px 4px #00000040;
}

